* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url("abstract-light-bg.77ba6a31.jpg") center / cover no-repeat fixed;
  padding: 12px;
  font-family: Lilita One, cursive;
  line-height: 1.5;
  display: flex;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

.title {
  background: linear-gradient(to right, #c44444 50%, #7f52b3 50%) 0 0 / 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 40px;
  margin-left: 32px;
  font-size: 36px;
  animation: textclip 10s linear infinite;
  display: block;
}

@keyframes textclip {
  to {
    background-position: 600%;
  }
}

.info {
  width: 50%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.theme-title {
  text-align: center;
  color: #fea9e3;
  text-shadow: 2px 2px 8px #000;
  margin-bottom: 16px;
  font-family: Lilita One, cursive;
}

.slider {
  width: 300px;
  height: 132px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin-bottom: 12px;
  overflow: hidden;
  box-shadow: 2px 2px 12px #000;
}

.imgs_slides {
  width: 400%;
  height: 100%;
  display: flex;
}

.imgs_slides input {
  display: none;
}

.slide {
  width: 25%;
  transition: all 2s;
}

.slide img {
  width: 300px;
  height: 100%;
}

fieldset {
  border: none;
  padding: 0;
}

.navigation {
  width: 300px;
  justify-content: center;
  margin-top: -28px;
  display: flex;
  position: absolute;
}

.navigation-btn {
  cursor: pointer;
  border: 2px solid #de6a23;
  border-radius: 10px;
  padding: 5px;
  transition: all 1s;
}

.navigation-btn:not(:last-child) {
  margin-right: 40px;
}

.navigation-btn:hover, .navigation-btn:checked {
  background: #de6a23;
}

#radio1:checked ~ .first {
  margin-left: 0;
}

#radio2:checked ~ .first {
  margin-left: -25%;
}

#radio3:checked ~ .first {
  margin-left: -50%;
}

#radio4:checked ~ .first {
  margin-left: -75%;
}

.searchQuery {
  width: 300px;
  height: 30px;
  float: left;
  color: #23083f;
  background: #fff;
  border: 0;
  border-radius: 4px;
  margin-bottom: 12px;
  padding: 10px;
  box-shadow: 2px 2px 12px #000;
}

.searchQuery:focus {
  outline: 0;
}

.start-btn, .restart-btn {
  width: 240px;
  height: 32px;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 1px 1px 2px #000;
  transition: background-color (--transition), border (--transition);
  background-color: #fea9e3;
  border: 1px solid #fea9e3;
  border-radius: 4px;
  margin: 0 auto 12px;
  font-family: inherit;
  font-size: 18px;
  box-shadow: 2px 2px 4px #000;
}

.start-btn:hover, .restart-btn:hover {
  background-color: #ff82d8;
  border: 1px solid #ff82d8;
}

.start-btn:active, .restart-btn:active {
  box-shadow: 0 0 12px #e1e1e1;
}

.start-btn:disabled {
  background-color: #cba3be;
  border: 1px solid #cba3be;
}

.notiflix-notify-failure {
  text-align: center;
}

.timer, .timer-best {
  color: #fff;
  text-shadow: 1px 1px 2px #000;
  margin-bottom: 16px;
}

.timer-description {
  margin-bottom: 4px;
  font-size: 24px;
}

.timer-clock {
  justify-content: center;
  gap: 12px;
  display: flex;
}

.timer-field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.timer-value {
  font-size: 36px;
}

.cards-list {
  width: 800px;
  flex-flow: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.cards-item {
  width: 120px;
  height: 176px;
  perspective: 400px;
}

.card {
  width: 100%;
  height: 100%;
  cursor: pointer;
  transform-style: preserve-3d;
  transform-origin: 100%;
  transition: transform 1s;
  position: relative;
}

.card.is-flipped {
  transform: translateX(-100%)rotateY(-180deg);
}

.card__face {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 20px;
  position: absolute;
  box-shadow: 2px 2px 4px #000;
}

.card__face--front {
  background-image: url("question-mark.4b4060d3.jpg");
  background-position: center;
  background-size: cover;
}

.card__face--back {
  background-color: #fde9f7;
  transform: rotateY(180deg);
}

.card__face--back img {
  height: 100%;
  border-radius: 20px;
}

.invisible {
  display: none;
}

